.App {
  text-align: center;
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  /* justify-content: center; */
  /* font-size: calc(10px + 2vmin); */
  color: white;
  print-color-adjust: exact;
}

body {
  overscroll-behavior: none;
}

.App-rows {
  text-align: left;
  margin-left: 2em;
  margin-right: 2em;
}

.App-rows p {
  margin-top: 1px;
  margin-bottom: 1em;
}

.App-row {
  /*display: inline-block;
  min-width: 8em;*/
  vertical-align: top;
}

.App-palette {
  display: inline-block;
  border-radius: 5px;
  outline: 1px solid #888;
  min-width: 3em;
  max-width: 3em;
  user-select: none;
}

.App-run:hover {
  border-radius: 4px;
  outline: 2px solid white;
  z-index: 3000;
  position: relative;
}

.App-run:focus {
  border: 2px solid #88f;
  border-radius: 4px;
  outline: 2px solid white;
  z-index: 3000;
  position: relative;
}

.App-run {
  display: inline-block;
  text-align: center;
  font-weight: 700;
  min-width: 2em;
  max-width: 2em;
  margin: 0.5px;
  border: 2px solid #888;
  user-select: none;
}

.App-grid {
  margin-top: 2em;
  image-rendering: "pixelated";
}
