
.Image-ctrl {
    margin: 10px;
}

.Image-palette {
    margin: 10px;
}

label, .Image-reset {
    margin-left: 12px;
    margin-right: 2px;
}

body {
    font-family: sans-serif;
    font-weight: 700;
}

body input {
    border-radius: 5px;
    padding: 4px 8px 4px 8px;
}

.Image-reset {
    font-family: sans-serif;
    font-weight: 700;
    border-radius: 5px;
    background-color: #f22;
    color: white;
    padding: 5px 10px 5px 10px;
}

.Image-palette span {
    margin: 4px;
    padding: 4px;
}

.Image-crochets {
    margin: 8px;
}

.Image-canvas {
    margin: 10px;
    border: 2px solid #555;
}